import request from '@/utils/request'

/**
 * 列表
 * @param query 查询参数，包含pageNum、pageSize以及其他参数
 */
export function fetchList (query) {
  return request({
    url: '/sys/scheduleJobs',
    method: 'get',
    params: query
  })
}

/**
 * 详情
 * @param id 主键
 */
export function fetchDetail (id) {
  return request({
    url: '/sys/scheduleJobs/' + id,
    method: 'get'
  })
}

/**
 * 添加
 * @param data 对象
 */
export function create (data) {
  return request({
    url: '/sys/scheduleJobs',
    method: 'post',
    data
  })
}

/**
 * 修改
 * @param data 对象
 */
export function update (data) {
  return request({
    url: '/sys/scheduleJobs/' + data.jobId,
    method: 'put',
    data
  })
}

/**
 * 删除
 * @param id 定时任务ID
 */
export function del (id) {
  return request({
    url: '/sys/scheduleJobs/' + id,
    method: 'delete'
  })
}

/**
 * 立即执行定时任务
 * @param jobIdAry 定时任务ID数组
 */
export function run (jobIdAry) {
  return request({
    url: '/sys/scheduleJobs/run',
    method: 'post',
    data: jobIdAry
  })
}

/**
 * 暂停定时任务
 * @param jobIdAry 定时任务ID数组
 */
export function pause (jobIdAry) {
  return request({
    url: '/sys/scheduleJobs/pause',
    method: 'post',
    data: jobIdAry
  })
}

/**
 * 恢复定时任务
 * @param jobIdAry 定时任务ID数组
 */
export function resume (jobIdAry) {
  return request({
    url: '/sys/scheduleJobs/resume',
    method: 'post',
    data: jobIdAry
  })
}



