<template>
  <div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0;">
      <el-form :inline="true" :model="listQuery" label-position="left">
        <el-form-item :label="$t('scheduleJobLog.jobId')">
          <el-input v-model="listQuery.jobId" :placeholder="$t('scheduleJobLog.jobId')" clearable @clear="listQuery.jobId = undefined"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" @click="handleFilter">{{$t('common.search')}}</el-button>
          <el-button type="danger" @click="$emit('back')">{{$t('common.back')}}</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-table ref="dataTable" :data="list" v-loading="listLoading" :element-loading-text="$t('common.loadingText')" border :fit="true"
              highlight-current-row stripe tooltip-effect="light">
      <el-table-column type="index" width="50" align="center"></el-table-column>
      <el-table-column :label="$t('scheduleJobLog.logId')" width="70" prop="logId" align="center"></el-table-column>
      <el-table-column :label="$t('scheduleJobLog.jobId')" width="70" prop="jobId" align="center"></el-table-column>
      <el-table-column :label="$t('scheduleJobLog.beanName')" prop="beanName" align="center"></el-table-column>
      <el-table-column :label="$t('scheduleJobLog.methodName')" prop="methodName" align="center"></el-table-column>
      <el-table-column :label="$t('scheduleJobLog.params')" prop="params" align="center"></el-table-column>
      <el-table-column :label="$t('scheduleJobLog.status')" align="center">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.status === 1" type="success">{{$t('common.success')}}</el-tag>
          <el-tag v-if="scope.row.status === 0" type="danger">{{$t('common.failure')}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column :label="$t('scheduleJobLog.error')" prop="error" align="center" show-overflow-tooltip></el-table-column>
      <el-table-column :label="$t('scheduleJobLog.times')" prop="times" align="center"></el-table-column>
      <el-table-column :label="$t('scheduleJobLog.createTime')" prop="createTime" align="center"></el-table-column>
    </el-table>
    <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                   :current-page="listQuery.pageNum" :page-sizes="[20,30,50,100]" :page-size="listQuery.pageSize"
                   layout="total, sizes, prev, pager, next, jumper" :total="total">
    </el-pagination>
  </div>
</template>
<script>
import {fetchList} from '@/api/scheduleJobLog'

export default {
  name: 'scheduleJobLog',
  props: ['jobId'],
  data () {
    return {
      list: null,
      total: null,
      listLoading: true,
      listQuery: {
        pageNum: 1,
        pageSize: 20,
        jobId: undefined
      }
    }
  },
  created () {
    this.listQuery.jobId = this.jobId
    this.getList()
  },
  methods: {
    handleSizeChange (val) {
      this.listQuery.pageSize = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.listQuery.pageNum = val
      this.getList()
    },
    handleFilter () {
      this.listQuery.pageNum = 1
      this.getList()
    },
    getList () {
      this.listLoading = true
      fetchList(this.listQuery).then(response => {
        this.list = response.data.list
        this.total = response.data.total
        this.listLoading = false
      })
    }
  }
}
</script>



