<template>
  <div>
    <div v-if="!showLogFlag">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0;">
        <el-form :inline="true" :model="listQuery" label-position="left">
          <el-form-item :label="$t('scheduleJob.beanName')">
            <el-input autofocus v-model="listQuery.beanName" :placeholder="$t('scheduleJob.beanName')" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button icon="el-icon-search" @click="handleFilter">{{$t('common.search')}}</el-button>
            <el-button icon="el-icon-plus" @click="handleCreate">{{$t('common.add')}}</el-button>
            <el-button icon="el-icon-edit" @click="handleUpdate">{{$t('common.edit')}}</el-button>
            <el-button icon="el-icon-delete" @click="del">{{$t('common.delete')}}</el-button>
            <el-button icon="el-icon-fa fa-pause" @click="pause">{{$t('common.pause')}}</el-button>
            <el-button icon="el-icon-fa fa-reply" @click="resume">{{$t('common.resume')}}</el-button>
            <el-button icon="el-icon-caret-right" @click="run">{{$t('common.run')}}</el-button>
            <el-button type="danger" @click="showLog">{{$t('common.log')}}</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-table ref="dataTable" :data="list" v-loading="listLoading" :element-loading-text="$t('common.loadingText')" border :fit="true"
                highlight-current-row stripe @selection-change="handleSelectionChange"
                @row-click="handleRowClick" tooltip-effect="light">
        <el-table-column type="selection" width="55" align="center"/>
        <el-table-column type="index" width="50" align="center"></el-table-column>
        <el-table-column :label="$t('scheduleJob.jobId')" width="70" prop="jobId" align="center"></el-table-column>
        <el-table-column :label="$t('scheduleJob.beanName')" prop="beanName" align="center"></el-table-column>
        <el-table-column :label="$t('scheduleJob.methodName')" prop="methodName" align="center"></el-table-column>
        <el-table-column :label="$t('scheduleJob.params')" prop="params" align="center"></el-table-column>
        <el-table-column :label="$t('scheduleJob.cronExpression')" prop="cronExpression" align="center"></el-table-column>
        <el-table-column :label="$t('scheduleJob.status')" align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status === 0" type="success">{{$t('common.normal')}}</el-tag>
            <el-tag v-if="scope.row.status === 1" type="danger">{{$t('common.pause')}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column :label="$t('scheduleJob.remark')" prop="remark" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column :label="$t('scheduleJob.createTime')" prop="createTime" align="center"></el-table-column>
      </el-table>
      <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                     :current-page="listQuery.pageNum" :page-sizes="[20,30,50,100]" :page-size="listQuery.pageSize"
                     layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>

      <el-dialog :close-on-click-modal="false" :title="dialogTitle" :visible.sync="dialogFormVisible" class="help-info">
        <el-form :rules="rules" ref="dataForm" :model="temp" label-position="right" label-width="120px" style='width: 90%;'>
          <el-form-item :label="$t('scheduleJob.beanName')" prop="beanName">
            <el-input v-model="temp.beanName" clearable></el-input>
            <el-alert title="Spring Bean名称；不能为空，长度应小于200个字符" :closable="false"/>
          </el-form-item>
          <el-form-item :label="$t('scheduleJob.methodName')" prop="methodName">
            <el-input v-model="temp.methodName" clearable></el-input>
            <el-alert title="方法名称不能为空；长度应小于100个字符" :closable="false"/>
          </el-form-item>
          <el-form-item :label="$t('scheduleJob.params')" prop="params">
            <el-input v-model="temp.params" clearable></el-input>
            <el-alert title="长度应小于2000个字符" :closable="false"/>
          </el-form-item>
          <el-form-item :label="$t('scheduleJob.cronExpression')" prop="cronExpression">
            <el-input v-model="temp.cronExpression" clearable></el-input>
            <el-alert title="秒 分 时 日 月 星期 年（可选）；不能为空，长度应小于100个字符" :closable="false"/>
          </el-form-item>
          <el-form-item :label="$t('scheduleJob.remark')" prop="remark">
            <el-input v-model="temp.remark" clearable></el-input>
            <el-alert title="长度应小于255个字符" :closable="false"/>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">{{$t('common.cancel')}}</el-button>
          <el-button v-if="dialogStatus === 'add'" type="primary" @click="createData">{{$t('common.confirm')}}</el-button>
          <el-button v-else type="primary" @click="updateData">{{$t('common.confirm')}}</el-button>
        </div>
      </el-dialog>
    </div>

    <schedule-job-log v-if="showLogFlag" @back="hideLog" :jobId="jobId"/>
  </div>
</template>
<script>
import {fetchList, create, update, del, run, pause, resume} from '@/api/scheduleJob'
import scheduleJobLog from './scheduleJobLog.vue'

export default {
  name: 'scheduleJob',
  components: {
    scheduleJobLog
  },
  data () {
    return {
      tableKey: 0, // 用于table结构改变时使用，此时无用
      list: null,
      total: null,
      listLoading: true,
      listQuery: {
        pageNum: 1,
        pageSize: 20,
        beanName: undefined
      },
      multipleSelection: [],
      temp: {
        jobId: undefined,
        beanName: undefined,
        methodName: undefined,
        params: undefined,
        cronExpression: undefined,
        remark: undefined
      },
      dialogFormVisible: false,
      dialogStatus: '',
      dialogTitle: '',
      rules: {
        beanName: [
          { required: true, message: 'bean名称不能为空', trigger: 'change' },
          { max: 200, message: '长度应小于200个字符', trigger: 'change' }
        ],
        methodName: [
          { required: true, message: '方法名称不能为空', trigger: 'change' },
          { max: 100, message: '长度应小于100个字符', trigger: 'change' }
        ],
        cronExpression: [
          { required: true, message: 'cron表达式不能为空', trigger: 'change' },
          { max: 100, message: '长度应小于100个字符', trigger: 'change' }
        ],
        params: [{ max: 2000, message: '长度应小于2000个字符', trigger: 'change' }],
        remark: [{ max: 255, message: '长度应小于255个字符', trigger: 'change' }]
      },
      showLogFlag: false,
      jobId: undefined
    }
  },
  created () {
    this.getList()
  },
  methods: {
    handleSizeChange (val) {
      this.listQuery.pageSize = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.listQuery.pageNum = val
      this.getList()
    },
    handleRowClick (row) {
      this.$refs.dataTable.toggleRowSelection(row)
    },
    handleSelectionChange (selection) {
      this.multipleSelection = selection.reduce((ary, cur, idx) => {
        ary.push(cur)
        return ary
      }, [])
    },
    handleFilter () {
      this.listQuery.pageNum = 1
      this.getList()
    },
    getList () {
      this.listLoading = true
      fetchList(this.listQuery).then(response => {
        this.list = response.data.list
        this.total = response.data.total
        this.listLoading = false
      })
    },
    resetTemp () {
      this.temp = {
        jobId: undefined,
        beanName: undefined,
        methodName: undefined,
        params: undefined,
        cronExpression: undefined,
        remark: undefined
      }
    },
    handleCreate () {
      this.resetTemp()
      this.dialogStatus = 'add'
      this.dialogTitle = this.$t('common.' + this.dialogStatus)
      this.dialogFormVisible = true
      this.$nextTick(() => {
        this.$refs.dataForm.clearValidate()
      })
    },
    createData () {
      const fm = this.$refs.dataForm
      fm.validate((valid) => {
        if (valid) {
          create(this.temp).then(() => {
            this.dialogFormVisible = false
            this.$message.success({
              message: '保存成功!',
              onClose: () => {
                this.getList()
              }
            })
          })
        }
      })
    },
    handleUpdate () {
      if (this.multipleSelection.length === 0) {
        this.$message.warning({
          message: '请选择一项任务！'
        })
        return
      }
      if (this.multipleSelection.length > 1) {
        this.$message.warning({
          message: '只能选择一项任务！'
        })
        return
      }
      this.temp = Object.assign({}, this.multipleSelection[0]) // copy obj
      this.dialogStatus = 'edit'
      this.dialogTitle = this.$t('common.' + this.dialogStatus)
      this.dialogFormVisible = true
      this.$nextTick(() => {
        this.$refs.dataForm.clearValidate()
      })
    },
    updateData () {
      const fm = this.$refs.dataForm
      fm.validate((valid) => {
        if (valid) {
          const tempData = Object.assign({}, this.temp)
          update(tempData).then(() => {
            this.dialogFormVisible = false
            this.$message.success({
              message: '修改成功！',
              onClose: () => {
                this.getList()
              }
            })
          })
        }
      })
    },
    del () {
      if (this.multipleSelection.length === 0) {
        this.$message.warning({
          message: '请选择一项任务！'
        })
        return
      }
      if (this.multipleSelection.length > 1) {
        this.$message.warning({
          message: '只能选择一项任务！'
        })
        return
      }
      del(this.multipleSelection[0].jobId).then(response => {
        if (response.code === 0) {
          this.$message.success({
            message: '删除成功！',
            onClose: () => {
              this.getList()
            }
          })
        }
      })
    },
    run () {
      if (this.multipleSelection.length === 0) {
        this.$message.warning({
          message: '至少选择一项任务！'
        })
        return
      }
      const jobIdAry = this.multipleSelection.map(job => {
        return job.jobId
      })
      run(jobIdAry).then(response => {
        if (response.code === 0) {
          this.$message.success({
            message: '执行成功！'
          })
        }
      })
    },
    pause () {
      if (this.multipleSelection.length === 0) {
        this.$message.warning({
          message: '至少选择一项任务！'
        })
        return
      }
      const jobIdAry = this.multipleSelection.map(job => {
        return job.jobId
      })
      pause(jobIdAry).then(response => {
        if (response.code === 0) {
          this.$message.success({
            message: '暂停成功！',
            onClose: () => {
              this.getList()
            }
          })
        }
      })
    },
    resume () {
      if (this.multipleSelection.length === 0) {
        this.$message.warning({
          message: '至少选择一项任务！'
        })
        return
      }
      const jobIdAry = this.multipleSelection.map(job => {
        return job.jobId
      })
      resume(jobIdAry).then(response => {
        if (response.code === 0) {
          this.$message.success({
            message: '恢复成功！',
            onClose: () => {
              this.getList()
            }
          })
        }
      })
    },
    showLog () {
      if (this.multipleSelection.length === 1) {
        this.jobId = this.multipleSelection[0].jobId
      }
      this.showLogFlag = true
    },
    hideLog () {
      this.multipleSelection = []
      this.jobId = undefined
      this.showLogFlag = false
    }
  }
}

</script>



